@font-face {
  font-family: "Avenir LT Std 45 Book";
  font-style: normal;
  font-weight: normal;
  src: url("./assets/fonts/AvenirLTStd-Book.woff") format("woff");
}

@font-face {
  font-family: "Number 23";
  font-style: normal;
  font-weight: normal;
  src: url("./assets/fonts/Number-23.otf") format("opentype");
}

* {
  box-sizing: border-box;
  color: #fff;
  font-family: "Number 23", sans-serif;
  font-size: 20px;
  font-weight: 400;
  line-height: 1;
  margin: 0;
  padding: 0;
  vertical-align: baseline;
}

body, html, #root {
  min-height: 100vh;
}

body {
  background: url("./assets/background_image.png") no-repeat center center fixed;
  background-size: cover;
}

#root {
  background-color: rgba(0, 0, 0, 0.2);
  height: 100%;
}
